.card-task {
  margin-bottom: 25px;
  width: 250px;
  color: #fff;
  background-color: #6c757d !important;
  border-color: #6c757d !important;
  cursor: move;
}

.stage-header {
  background-color: #ccc !important;
}
.card-task-option i {
  color: #fff !important;
  font-size: 14px;
}

.card-task-option {
  display: inline !important;
  float: right !important;
  cursor: pointer !important;
}

.column-wrapper {
  margin: 0px 20px;
  padding: 0px 20px;
  overflow-x: auto;
}

.inner-column-wrapper {
  width: 2350px;
}

.card-column {
  float: left;
  margin-left: 20px;
  width: 290px;
}
.card-column:first-of-type {
  margin-left: 0;
}
